.char-tile {
  text-align: center;
}

.img-thumbnail {
  margin-top: 1.59rem;
  margin-bottom: 1.59rem;
  height: 12rem;
  width: 12rem;
  object-position: top center;
  object-fit: cover;
  box-shadow:  0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  transition: all 0.2s ease;
}

.img-thumbnail:hover {
  margin-top: .59rem;
  margin-bottom: .59rem;
  height: 14rem;
  width: 14rem;
  box-shadow: 0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);
  cursor: pointer;
}